
@import 'reset';



// 1920 w by 1080 h tv
// not responsive 

* {
	box-sizing: border-box;
	text-decoration: none;
}

body {
	background: #fff;
	width: 1290px;
	height: 1080px;
	overflow-x: hidden;
	overflow-y: hidden;
}


// home page--------------------------------------------------

.logo{
	display: flex;
	position: absolute;
	width: 100px;
	margin: 50px;
	opacity: 50%;
	display: none; /* erase this */  
	
	img{
		z-index: 1;
		position: relative;
		justify-content: center;
		width: 100%;
		height: 100%;
	}
}

.bigVideo {
	position: absolute;
	width: 100%;
	z-index: 0;
	display: none; /* erase this */  
}


// home page big icon buttons-----------------------------------

.home-boxes{
	display: flex;
	flex-wrap: wrap;
	width: 1260px;
	height: 1020px;
	margin: 50px auto;

	.col{
		margin: 0 auto;

		img{
			
		}
	}

}


// bottom nav icons------------------------------------------

nav{
	width: 90%;
	position: fixed;
	bottom: 45px;
	
	ul{
		display: flex;

		li{
			font-size: 34px;
			text-align: center;

			figure{
				padding: 10px 57px;
			}

			a{
				color: gray;

				&:active {
					color: #e0586f;
				}
			}
		}
	}

}


//h1 headings----------------------------------

.horses-heading,
.timeline-heading,
.fashion-heading,
.facts-heading{
	text-align: center;
	display: flex;
	justify-content: center;

	h1{
		font-size: 70px;
		margin-top: 90px;

	}
}


// slick plugin horses carousel-------------------------------


section.center.slider.slick-initialized.slick-slider{
	width: 90%;
	margin: 80px auto;
	display: flex;

	div{
		justify-content: center;

		img{
			padding-bottom: 40px;
			margin: 0 auto;
		}
	}

	//next button
	button.slick-next.slick-arrow{
		right: 30px;
		top: 240px;
		z-index: 2;

		&::before{
			font-size: 50px;
			color: gray;
		}
	}

	//previous button
	button.slick-prev.slick-arrow{
		left: 10px;
		top: 240px;
		z-index: 2;

		&::before{
			font-size: 50px;
			color: gray;
		}
	}
}


	



// timeline css ---------------------------------------------


section.dates.slider.slick-initialized.slick-slider{
	width: 90%;
	margin: 80px auto;

	div{
		justify-content: center;
		display: flex;
		flex-direction: row;

		a{
			font-size: 33px;
			color: #000;
			padding: 0 30px;
		}			
	}

}


#content{
	margin: auto 200px;
	padding: 45px 0;
	display: flex;
	overflow: hidden;

	li{
		padding: 0 40px;

		img{
			float: left;
			margin: 20px;

		}

		h1{
			margin-top: 20px;
			font-size: 39px;
			font-weight: bold;
		}

		p{
			width: 840px;
			padding: 20px 30px 200px 30px;
			line-height: 29px;
			font-size: 25px;
			orphans: none;
			widows: none;
			hyphens: none;
		}
	}
}



