// slick plugin facts carousel-------------------------------


section.fact.slider.slick-initialized.slick-slider{
	width: 95%;
	margin: 80px auto;
	display: flex;

	div{
		justify-content: center;

		img{
			margin: 80px 210px;
		}
	}

	//next button
	button.slick-next.slick-arrow{
		right: 90px;
		top: 280px;
		z-index: 2;

		&::before{
			font-size: 50px;
			color: gray;
		}
	}

	//previous button
	button.slick-prev.slick-arrow{
		left: 50px;
		top: 280px;
		z-index: 2;

		&::before{
			font-size: 50px;
			color: gray;
		}
	}
}


// slick plugin fashion carousel-------------------------------

section.hat.slider.slick-initialized.slick-slider{
	width: 92%;
	margin: 80px auto;
	display: flex;
	justify-content: center;

	div{
		width: 88%;
		justify-content: center;

		img{
			margin: 95px auto;
			padding: 0 10px;
		}
	}

	//next button
	button.slick-next.slick-arrow{
		right: 25px;
		top: 240px;
		z-index: 2;

		&::before{
			font-size: 50px;
			color: gray;
		}
	}

	//previous button
	button.slick-prev.slick-arrow{
		left: 0px;
		top: 240px;
		z-index: 2;

		&::before{
			font-size: 50px;
			color: gray;
		}
	}
}


// garland of roses --------------------------------------

.rose-content{
	width: 88%;
	margin: 30px auto;

	.rose-text-column{
		width: 70%;
		display: flex;
		flex-direction: column;
		margin: 0 auto;
		text-align: center;

		p{
			line-height: 1.3em;
			font-size: 22px;
			padding: 10px 0;
		}
	}

	.h2-dates-and-ps{
		width: 100%;
		display: flex;
		flex-direction: column;
		margin: 40px auto;

		div{
			display: flex;
			justify-content: center;
			text-align: center;
		
			h2{
				font-size: 33px;
				font-weight: bold;
				padding: 0 107px;
			}

			p{	
				width: 280px;
				padding: 10px 20px;
				font-size: 22px;
				line-height: 1.2em;
			}
		}


	}

}


